export function callRaspPage(e) {
    const { user, ps}   = getUser()
    let targetDay = e.srcElement.dataset.day
    let url       = APP_CONSTANTS.bookingsTimetableUrl
    let params    = {
        'wtime':    targetDay,
        'login':    user,
        'password': ps
    }

    let qString   = ''
    for (let param in params){
        qString  += `&${param}=${params[param]}`
    }
    let reqString = url + qString
    window.open(reqString)
}